@keyframes drop {
  0% {
    margin-bottom: 100%;
    opacity: 0;
  }

  90% {
    margin-bottom: -10%;
  }

  100% {
    margin-bottom: 0;
    opacity: 1;
  }
}

@keyframes shiftLeft {
  0% {
    transform: translateX(0%);
    opacity: 1;
  }

  100% {
    transform: translateX(100%);
    opacity: 0;
  }
}

@keyframes shake {
  0% {
    transform: translateX(10%) rotate(5deg);
  }
  10% {
    transform: translateX(-10%) rotate(-5deg);
  }
  20% {
    transform: translateX(10%) rotate(5deg);
  }
  30% {
    transform: translateX(-10%) rotate(-5deg);
  }
  40% {
    transform: translateX(10%) rotate(5deg);
  }
  50% {
    transform: translateX(-10%) rotate(-5deg);
  }
  60% {
    transform: translateX(10%) rotate(5deg);
  }
  70% {
    transform: translateX(-10%) rotate(-5deg);
  }
  80% {
    transform: translateX(10%) rotate(5deg);
  }
  90% {
    transform: translateX(-10%) rotate(-5deg);
  }
  100% {
    transform: translateX(0%) rotate(0deg);
  }
}

