*, :before, :after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

@media only screen and (min-width: 112em) {
  html {
    font-size: 75%;
  }
}

@media only screen and (max-width: 62.5em) {
  html {
    font-size: 56.25%;
  }
}

@media only screen and (max-width: 37.5em) {
  html {
    font-size: 50%;
  }
}

body {
  box-sizing: border-box;
}

.container {
  grid-template-rows: min-content minmax(85vh, min-content) min-content;
  grid-template-columns: [full-start] minmax(3rem, 1fr)[center-start] repeat(6, [col-start] minmax(min-content, 19rem)[col-end])[center-end] minmax(3rem, 1fr)[full-end];
  align-items: center;
  display: grid;
}

body {
  color: #1f2939;
  font-family: Open Sans, sans-serif;
  font-size: 1.4rem;
  font-weight: 400;
  line-height: 1;
}

.heading-primary {
  font-size: 7.2rem;
  font-weight: 800;
  line-height: .8;
}

.heading-secondary {
  font-size: 3.6rem;
  font-weight: 600;
}

.paragraph {
  font-weight: 400;
  line-height: 2;
}

.paragraph--accent {
  color: #ff4d4f;
  font-weight: 600;
}

@keyframes drop {
  0% {
    opacity: 0;
    margin-bottom: 100%;
  }

  90% {
    margin-bottom: -10%;
  }

  100% {
    opacity: 1;
    margin-bottom: 0;
  }
}

@keyframes shiftLeft {
  0% {
    opacity: 1;
    transform: translateX(0%);
  }

  100% {
    opacity: 0;
    transform: translateX(100%);
  }
}

@keyframes shake {
  0% {
    transform: translateX(10%)rotate(5deg);
  }

  10% {
    transform: translateX(-10%)rotate(-5deg);
  }

  20% {
    transform: translateX(10%)rotate(5deg);
  }

  30% {
    transform: translateX(-10%)rotate(-5deg);
  }

  40% {
    transform: translateX(10%)rotate(5deg);
  }

  50% {
    transform: translateX(-10%)rotate(-5deg);
  }

  60% {
    transform: translateX(10%)rotate(5deg);
  }

  70% {
    transform: translateX(-10%)rotate(-5deg);
  }

  80% {
    transform: translateX(10%)rotate(5deg);
  }

  90% {
    transform: translateX(-10%)rotate(-5deg);
  }

  100% {
    transform: translateX(0%)rotate(0);
  }
}

@keyframes explosion {
}

.hidden {
  opacity: 0;
  visibility: hidden;
}

.burger-demo-box {
  height: min(80vh, 60rem);
  grid-column: 2 / 4;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

@media only screen and (max-width: 62.5em) {
  .burger-demo-box {
    grid-area: 2 / 1 / 3 / -1;
  }
}

.burger-demo-box:before {
  content: "";
  width: 70%;
  height: 100%;
  background-image: url("bg-main.c8a7e38a.svg");
  background-repeat: no-repeat;
  background-size: contain;
  position: absolute;
  top: 10%;
}

@media only screen and (max-width: 37.5em) {
  .burger-demo-box:before {
    height: 50%;
    top: 20%;
  }
}

.burger-demo {
  width: 50%;
  aspect-ratio: 1;
  opacity: 0;
  animation-name: drop, shake;
  animation-duration: 1s, .5s;
  animation-timing-function: ease, ease;
  animation-iteration-count: 1, 1;
  animation-direction: normal, normal;
  animation-play-state: running, running;
  animation-delay: 1s, 2s;
  animation-fill-mode: forwards;
  position: relative;
}

.burger-demo > * {
  width: 100%;
  transition: all .5s -.2s;
  position: absolute;
  left: 0;
}

.burger-demo > * img {
  width: 100%;
}

