.summary__btn, .form__btn, .burger-demo__btn, .card__btn {
  text-align: center;
  color: #fff;
  cursor: pointer;
  background-color: #5243c2;
  border: 1px solid #0000;
  border-radius: 100rem;
  justify-content: center;
  align-items: center;
  padding: 1.5rem 2.5rem;
  font-family: inherit;
  font-size: 1.6rem;
  font-weight: 600;
  text-decoration: none;
  transition: all .3s;
  display: flex;
}

.summary__btn:hover, .form__btn:hover, .burger-demo__btn:hover, .card__btn:hover, .summary__btn:active, .form__btn:active, .burger-demo__btn:active, .card__btn:active {
  background-color: #6456c8;
}

.summary__btn:disabled, .form__btn:disabled, .burger-demo__btn:disabled, .card__btn:disabled {
  color: #5243c2;
  cursor: default;
  background-color: #0000;
  border: 1px solid #5243c2;
}

.card__btn {
  height: 3rem;
  aspect-ratio: 1;
  color: #5243c2;
  background-color: #f5f5ff;
  border-radius: 50%;
  padding: 0;
  font-size: 2rem;
}

.card__btn:hover {
  background-color: #dcdcff;
}

.burger-demo__btn {
  width: min(20vh, 15rem);
  aspect-ratio: 1;
  text-transform: uppercase;
  padding: 2rem;
  font-weight: 800;
  line-height: 1.2;
}

.card {
  width: 12rem;
  border: 1px solid #f0f7fa;
  border-radius: 2rem;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  padding: 1.5rem;
  font-weight: 600;
  display: flex;
}

.card__img-box {
  height: 4rem;
  align-items: center;
  display: flex;
}

.card__img {
  width: 100%;
  object-fit: cover;
}

.card__controls {
  align-items: center;
  gap: 1rem;
  display: flex;
}

.card__title {
  text-transform: capitalize;
}

.modal {
  width: 100%;
  height: 100vh;
  z-index: 2000;
  position: fixed;
  top: 0;
  left: 0;
}

.modal__content {
  width: min(60rem, 80vw);
  min-height: 10rem;
  z-index: 10000;
  background-color: #fff;
  border-radius: 1.5rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal__backdrop {
  height: 100%;
  -webkit-backdrop-filter: blur(.5rem);
  backdrop-filter: blur(.5rem);
  background-color: #5243c2cc;
}

.modal__close {
  width: 4rem;
  aspect-ratio: 1;
  cursor: pointer;
  color: #1f2939;
  background-color: #0000;
  border: none;
  font-size: 3.5rem;
  transition: all .3s;
  position: absolute;
  top: 1rem;
  right: 1rem;
}

.modal__close:hover, .modal__close:active {
  color: #5243c2;
}

.form__header {
  border-bottom: 2px solid #f5f5ff;
  align-items: center;
  gap: 1.5rem;
  padding: 1.5rem;
  display: flex;
}

.form__header-icon {
  width: 3rem;
  aspect-ratio: 1;
  fill: #1f2939;
}

.form__title {
  font-size: 2.7rem;
  font-weight: 600;
}

.form__set {
  border: none;
  border-bottom: 2px solid #f5f5ff;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
  padding: 1.5rem 1.5rem 2rem;
  display: grid;
}

@media only screen and (max-width: 62.5em) {
  .form__set {
    grid-template-columns: 1fr;
    gap: 2rem;
    padding-block: 2.5rem;
  }
}

.form__group {
  position: relative;
}

.form__input[data-type="time"] {
  appearance: none;
  cursor: pointer;
  background: url("arrow-down.5761a1d2.svg") right 1rem center no-repeat;
}

.form__input[data-type="time"] ~ .form__label {
  cursor: default;
}

.form__input {
  width: 100%;
  height: 5.5rem;
  color: inherit;
  background: none;
  border: 1px solid #f5f5ff;
  border-radius: 1rem;
  padding-left: 4rem;
  font-family: inherit;
  font-size: 1.6rem;
  font-weight: 600;
  transition: all .3s;
  display: block;
}

.form__input:focus {
  border: 1px solid #5243c2;
  outline: none;
}

.form__input::placeholder {
  opacity: 0;
}

.form__input:not(:placeholder-shown) {
  padding-top: 1.75rem;
}

.form__input--invalid {
  border: 1px solid #ff4d4f;
}

.form__label {
  color: #949ba6;
  cursor: text;
  font-size: 1.4rem;
  transition: all .3s;
  display: block;
  position: absolute;
  top: .75rem;
  left: 4rem;
}

.form__invalid-message {
  opacity: 0;
  color: #ff4d4f;
  font-size: 1.2rem;
  transition: all .3s;
  position: absolute;
  top: 105%;
  left: 1rem;
}

.form__input:placeholder-shown ~ .form__label {
  font-size: 1.6rem;
  top: 2rem;
}

.form__icon {
  width: 2rem;
  aspect-ratio: 1;
  fill: #949ba6;
  transition: all .3s;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(50%, -50%);
}

.form__input:not(:placeholder-shown).form__input:not(.form__input--invalid) ~ .form__icon, .form__input:focus ~ .form__icon {
  fill: #5243c2;
}

.form__input--invalid ~ .form__icon {
  fill: #ff4d4f;
}

.form__input--invalid ~ .form__invalid-message {
  opacity: 1;
}

.form__controls {
  justify-content: end;
  gap: 1.5rem;
  padding: 1.5rem;
  display: flex;
}

.form__btn {
  width: 12rem;
  padding: 1.5rem 0;
}

.form__btn--cancel {
  color: #5243c2;
  background-color: #f5f5ff;
}

.form__btn--cancel:hover, .form__btn--cancel:active {
  color: currentColor;
  background-color: #dcdcff;
}

.tooltip {
  background-color: #fff;
  border-radius: 1rem;
  align-items: center;
  gap: 1rem;
  padding: 1.5rem 2rem;
  font-size: 2rem;
  font-weight: 600;
  transition: all .3s;
  display: inline-flex;
  position: absolute;
  top: 5%;
  right: 0;
  box-shadow: 0 0 2rem .5rem #0000001a;
}

.tooltip:before {
  content: "";
  width: 0;
  height: 0;
  border-bottom: 1rem solid #fff;
  border-left: 1rem solid #0000;
  border-right: 1rem solid #0000;
  display: block;
  position: absolute;
  top: -1rem;
  left: 3rem;
}

.tooltip__emoji {
  width: 3rem;
  height: 3rem;
}

.header {
  height: 10rem;
  grid-column: center-start / center-end;
  gap: 3rem;
  padding: 1.5rem;
  display: flex;
  position: relative;
}

.logo__img {
