.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 2000;

  &__content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: MIN(60rem, 80vw);

    min-height: 10rem;
    background-color: $color-white;
    border-radius: 1.5rem;
    z-index: 10000;
  }

  &__backdrop {
    height: 100%;
    background-color: rgba($color-blue, 0.8);
    backdrop-filter: blur(0.5rem);
  }

  &__close {
    position: absolute;
    top: 1rem;
    right: 1rem;
    font-size: 3.5rem;
    width: 4rem;
    aspect-ratio: 1;
    background-color: transparent;
    border: none;
    cursor: pointer;
    color: $color-black;
    transition: all $anim-duration;

    &:hover,
    &:active {
      color: $color-blue;
    }
  }
}
